.transction-submitted {
  text-align: center;
  background: #2B2C2B;
  border: 1px solid #1B1B1B;
  @media #{$desktop} {
    width: 400px;
  }
  @media #{$mdWidth} {
    width: 400px;
  }
  @media #{$smWidth} {
    width: 95%;
    min-width: 320px;
  }
  @media #{$xsWidth} {
    width: 95%;
    min-width: 320px;
  }
  &__logo {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin: 24px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(250, 208, 106, 0.5);

    @media #{$smWidth} {
      width: 80px;
      height: 80px;
    }

    svg {
      fill: #000;
    }
  }

  .modal__title {
    margin: 0;
  }

  &__link {
    text-align: center;
    margin: 12px 0;
  }

  &__tip {
    margin-top: 13px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #22292f;
    line-height: 28px;
    max-width: 300px;
    text-align: left;
  }

  &__loading {
    display: flex;
    justify-content: center;

    img {
      width: 24px;
      height: 24px;
      -webkit-animation: loading 3s linear infinite;
    }
  }

  &__status {
    display: flex;
    justify-content: center;

    img {
      width: 48px;
      height: 48px;
    }
  }

  &__text {
    font-size: 14px;
    color: #ffffff;
    margin: 0px 0 30px;
    text-align: center;
  }

  &__btn {
    //background: #0166c5;;
    //border-radius: 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px 16px;
    position: static;
    width: 130px;
    height: 36px;
    right: 187px;
    bottom: 0px;
    background: #7ABFFF;
    border-radius: 24px;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: auto;

    @media #{$smWidth} {
      margin-top: 24px;
    }
  }
}

.transction-submitted-box {
  @media #{$desktop} {
    width: 480px;
  }
  @media #{$mdWidth} {
    width: 480px;
  }
  @media #{$smWidth} {
    width: 100%;
    min-width: 320px;
  }
  @media #{$xsWidth} {
    width: 100%;
    min-width: 320px;
  }
}

@keyframes loading {
  to {
    transform: rotate(360deg);
  }
}
